@import '../../theme/variables.scss';

.app {
  .brand {
    $size: 6em;
    position: absolute;
    top: -10px;
    left: 0px;
    display: inline-block;
    background: $peek_yellow url('../Home/logo.png') no-repeat center bottom;
    width: $size;
    height: $size + 0.5em;
    background-size: 100%;
    margin: 0 10px 0 0;
    transition: top 0.5s;

    @media (max-width: $screen-xs-max) {
      width: 4em;
      height: 4.5em;
      left: 15px;
    }

    &:hover{
      top: 0px;
    }
  }
  nav :global(.fa) {
    font-size: 2em;
    line-height: 20px;
  }
  .regionDropdown{
    &:hover{
      .dropdown{
        @extend .open;
      }
    }
  }
  .footer {
    bottom: -20em;
    width: 100%;
    height: auto;
    background-color: $peek_light_gray;
    color: $text-color;
    margin-top: 100px;

    :global(a){
      color: $text-color;
    }

    .mobile{
      background-image: url(https://dxvgidz67iahm.cloudfront.net/assets/icons-sf65c98ad92-d23ddbafb5cbc5c885c78417ad1237b1.png);
      background-position: -404px -57px;
      background-repeat: no-repeat;
      overflow: hidden;
      height: 253px;
      width: 118px;
      display: block;
      margin: auto;
      position: absolute;
      top: -75px;
      right: -50px;
    }
    .appstore{
      background-image: url(https://dxvgidz67iahm.cloudfront.net/assets/icons-sf65c98ad92-d23ddbafb5cbc5c885c78417ad1237b1.png);
      background-repeat: no-repeat;
      overflow: hidden;
      display: block;
      background-size: 263px 784px;
      background-position: 0 -339px;
      height: 57px;
      width: 200px;
      margin: auto;
    }
  }

}
.appContent {
  margin: 50px auto; // for fixed navbar
  max-width: 1200px;
}



