@import "../../theme/variables.scss";
@import "../../theme/helpers.scss";

:local .activityInner{
  background-color: $peek_black;
  color: #fff;
}

:local .activityWrap{
  margin-bottom: 1em;
  padding: 1.5em;
  border-bottom: 1px solid darken($peek_gray, 30%);

  &:last-child{
    border: none;
  }
}

:local .activityName{
  font-family: $font_sofia_regular;
  font-size: 1.2em;
}

:local .activityTagline{
  font-family: $font_sofia_light;
  font-size: .9em;
  margin: .4em 0em;
  line-height: 1.2em;
}

:local .activityCta{
  font-size: 1.2em;
  width: 61.8%;

}



