@import "../../../theme/variables.scss";
@import "../../../theme/helpers.scss";

:local .activityBlock{
  background: $peek_light_gray;
  border: 1px solid $peek_gray;
  @extend .padding-md;
  @extend .margin-md-v;

  :local .priceTagWrap{
    background: $peek_yellow;
    color: $peek_light_black;
    position: absolute;
    top: -0.75em; // This is the padding on the details block
    right: 0;
    font-family: $font_sofia_regular;
    font-size: 1.25em;
    text-align: center;
    display: inline-block;
    padding: 0.35em 0.85em;

    @media (max-width: $screen-xs-max) {
      top: -1.95em;
      right: -1em;
    }

    :global(strong) {
      @extend .block;
      font-family: $font_sofia_bold;
      font-size: 1.6em;
      color: $peek_black;
    }
  }

  :local .activityName{
    font-family: $font_sofia_bold;
    font-weight: normal;
    font-size: 2em;
    color: $peek_black;
    @extend .padding-lg-right;
  }

  :local .activityTagline{
    color: #111;
    font-family: $font_sofia_bold;
    font-weight: normal;
    font-size: 1.25em;
    line-height: 1.75em;
    @extend .padding-lg-right;
  }

  :local .activitySummary,
  :local .activityDescription{
    color: $peek_black;
    font-family: $font_sofia_regular;
    font-size: 1.5em;
    line-height: 1.75em;
    ul{
      margin: 1em;
      li{
        list-style-type: disc;
      }
    }
  }

  :local .activityDetailsCta,
  :local .activityBookCta{
    display: none;
  }

  :local .activityDetailsIcons{
    @extend .padding-md;
    border-top: 1px solid $peek_gray;
    border-bottom: 1px solid $peek_gray;

    :global(.detail-info) {
      border-right: 1px solid $peek_gray;
      height: 8.5em; // I really don't like this, but its for making them same size.

      &:last-child{
        border-right: none;
      }

      @media (max-width: $screen-xs-max) {
        border-right: none;
      }
    }

    :global(.icon){
      font-size: 2em;
      @extend .block;
    }
  }
}



