.app .brand {
  position: absolute;
  top: -10px;
  left: 0px;
  display: inline-block;
  background: #FFF200 url("../Home/logo.png") no-repeat center bottom;
  width: 6em;
  height: 6.5em;
  background-size: 100%;
  margin: 0 10px 0 0;
  transition: top 0.5s;
}

@media (max-width: 767px) {
  .app .brand {
    width: 4em;
    height: 4.5em;
    left: 15px;
  }
}

.app .brand:hover {
  top: 0px;
}

.app nav :global(.fa) {
  font-size: 2em;
  line-height: 20px;
}

.app .footer {
  bottom: -20em;
  width: 100%;
  height: auto;
  background-color: #F0F0F0;
  color: #333;
  margin-top: 100px;
}

.app .footer :global(a) {
  color: #333;
}

.app .footer .mobile {
  background-image: url(https://dxvgidz67iahm.cloudfront.net/assets/icons-sf65c98ad92-d23ddbafb5cbc5c885c78417ad1237b1.png);
  background-position: -404px -57px;
  background-repeat: no-repeat;
  overflow: hidden;
  height: 253px;
  width: 118px;
  display: block;
  margin: auto;
  position: absolute;
  top: -75px;
  right: -50px;
}

.app .footer .appstore {
  background-image: url(https://dxvgidz67iahm.cloudfront.net/assets/icons-sf65c98ad92-d23ddbafb5cbc5c885c78417ad1237b1.png);
  background-repeat: no-repeat;
  overflow: hidden;
  display: block;
  background-size: 263px 784px;
  background-position: 0 -339px;
  height: 57px;
  width: 200px;
  margin: auto;
}

.appContent {
  margin: 50px auto;
  max-width: 1200px;
}



