@import '../../theme/variables.scss';

:local .container{
  width: 100%;
  height: 250px;
  display: block;
  background-size: cover;

  &:hover{
    box-shadow: 0px 0px 10px rgba(0,0,0,0.5);
  }
}

:local .priceTag{
  background: url(../../../static/price-badge.png) no-repeat center center;
  position: absolute;
  background-size: contain;
  top: 5px;
  right: 21px;
  display: block;
  height: 100px;
  width: 80px;
  text-align: center;
  padding-top: 40px;
  color: $text-color;
  line-height: 120%;

  :global(.block){
    height: 15px;
  }
  :global(.currency){
    vertical-align: top;
    display: inline-block;
  }
  :global(.price){
    vertical-align: bottom;
    display: inline-block;
    font-size: 1.5em;
    padding-top: 5px
  }
}

:local .content{
  position: absolute;
  bottom: 0px;
  left: 0px;
  right: 0px;
  color: #fff;
  text-shadow: rgb(0, 0, 0) 1px 0px 4px, rgb(85, 85, 85) -1px -1px 4px;

  :local .background{
    background: linear-gradient(to bottom, transparent 0%, rgba(0,0,0,0.8) 100%);
    padding: 1em;
  }
}



