@import '../../../theme/variables.scss';

:local .day{
  width: 2.25em;
  height: 2.25em;
  line-height: 2.25em;
  margin: 2px;
  display: inline-block;

  &:global(.available){
    background: lighten($brand-success, 10%);
    color: #fff;
    cursor: pointer;
  }

  &:global(.selected){
    background: $brand-success;
  }

  &:global(.today){
    background: $peek_yellow;
    color: $peek_black;
  }
}

:local .header{
  :global(.fa){
    padding: 0.25em;
    background: $peek_light_gray;
    cursor: pointer;
    height: 2em;
    width: 2em;
    border-radius: 50%;
    line-height: 1.5em;

    &:hover{
      opacity: 0.75;
    }
  }
}

:local .monthLabel{
  font-weight: 500;
  font-size: 1.25em;
}



